import ButtonRouter from "../../../common/button/ButtonRouter";
import Logo from "../../../common/logo/Logo";
import styles from "./BannerVideo.module.css";

function BannerVideo() {

    return (
        <div className={styles.bannerVideo}>

            <Logo size="large" />

            <p className={styles.title}>Pipoca gil - Podcast</p>
            <p className={styles.by}>Com Ibson Cabral</p>

            <div className={styles.description}>
                <p>Esse podcast  voltado para o mundo gil de desenvolvimento de software. Tratamos de todos os assuntos relacionados a transformao digital como um todo e suas vertentes</p>
            </div>

            <ButtonRouter to="/ta" size="custom-size-adaptive" color="custom-colors-red" text="Ver todos os episdios do podcast" />

        </div>
    );
};

export default BannerVideo;