import styles from "./About.module.css"

import image6 from "../../../assets/images/photos/image6.png";
import instagramIcon from "../../../assets/images/icons/socialMedia/instagram.png";
import linkedinIcon from "../../../assets/images/icons/socialMedia/linkedin.png";
import facebookIcon from "../../../assets/images/icons/socialMedia/facebook.png";
import xIcon from "../../../assets/images/icons/socialMedia/x.png";
import youtubeIcon from "../../../assets/images/icons/socialMedia/youtube.png";

import { Link as RouterLink } from "react-router-dom";
import ButtonRouter from "../../common/button/ButtonRouter";

function About() {
    return (
        <div className={styles.about} id="about">

            <section className={styles.content}>

                <header className={styles.header}>
                    <p>Sobre Ibson Cabral e Pipca gil</p>

                    <h2>Ttulo da biografia e porque devem escut-lo</h2>

                </header>

                <aside className={styles.contentBiography}>
                    <article className={styles.leftSection}>
                        <img src={image6} loading="lazy" alt="ibson" />
                    </article>

                    <article className={styles.rightSection}>
                        <section className={styles.biography}>
                            <p>Sou por natureza um Facilitador e Entusiasta da Transformao gil e Digital.
                                Atuando ativamente no Movimento gil como um todo.</p>
                            <p>Idealizei e materializei o Podcast Pipoca gil, com o propsito de ser um
                                podcast "cho de fbrica", ajudando na divulgao de contedo do Mundo gil e contando casos e solues para a implementao da Transformao Digital nas empresas.</p>
                            <p>Sou Head da Jornada Talks da Jornada Colaborativa..Co-autor do Livro de Inovao da Jornada Colaborativa.Co-autor do Livro Jornada Colaborativa.Sou Diretor de Comunicao e Marketing da IIBA BrasilSou o Produtor do Podcast do IIBA Brasil.</p>
                        </section>

                        <ButtonRouter to="/readMore" size="custom-size-small" color="custom-colors-transparent" text="Ler mais" />

                        <div className={styles.socialMedia}>

                            <p>Mdia Sociais</p>

                            <figure>
                                <a href="https://www.instagram.com/pipocaagil/" target="_blank"><img src={instagramIcon} alt="instagram" /></a>
                                <a href="https://www.linkedin.com/company/pipoca-%C3%A1gil/about/" target="_blank"><img src={linkedinIcon} alt="linkedin" /></a>
                                <a href="https://www.facebook.com/pipocaagil/" target="_blank"><img src={facebookIcon} alt="facebook" /></a>
                                <a href="https://twitter.com/pipoca_agil" target="_blank"><img src={xIcon} alt="x" /></a>
                                <a href="https://www.youtube.com/channel/UCb2_j0yVyHR-Djxn7gyycqw" target="_blank"><img src={youtubeIcon} alt="youtube" /></a>
                            </figure>
                        </div>
                    </article>
                </aside>
            </section>
        </div>
    );
};

export default About;